import React, { Component } from 'react';
import Sidebar from '../sidebar/sidebar';
import Character from '../character/character';
import './character-grid.css';

const characterList = ['MARIO', 'DONKEY KONG', 'LINK', 'SAMUS', 'DARK SAMUS', 'YOSHI', 'KIRBY', 'FOX', 'PIKACHU',
    'LUIGI', 'NESS', 'CAPTAIN FALCON', 'JIGGLYPUFF', 'PEACH', 'DAISY', 'BOWSER', 'ICE CLIMBERS', 'SHEIK',
    'ZELDA', 'DR. MARIO', 'PICHU', 'FALCO', 'MARTH', 'LUCINA', 'YOUNG LINK', 'GANONDORF', 'MEWTWO', 'ROY',
    'CHROM', 'MR. GAME & WATCH', 'META KNIGHT', 'PIT', 'DARK PIT', 'ZERO SUIT SAMUS', 'WARIO', 'SNAKE',
    'IKE', 'POKÃ‰MON TRAINER', 'DIDDY KONG', 'LUCAS', 'SONIC', 'KING DEDEDE', 'OLIMAR', 'LUCARIO', 'R.O.B.',
    'TOON LINK', 'WOLF', 'VILLAGER', 'MEGA MAN', 'Wii Fit TRAINER', 'ROSALINA & LUMA', 'LITTLE MAC', 'GRENINJA',
    'PALUTENA', 'PAC-MAN', 'ROBIN', 'SHULK', 'BOWSER JR.', 'DUCK HUNT', 'RYU', 'KEN', 'CLOUD', 'CORRIN', 'BAYONETTA',
    'INKLING', 'RIDLEY', 'SIMON', 'RICHTER', 'KING K. ROOL', 'ISABELLE', 'INCINEROAR', 'Mii Brawler', 'Mii Swordfighter', 'Mii Gunner' ];

export default class CharacterGrid extends Component {
    constructor() {
        super();
        let stateObject = { fighters: {} };
        characterList.forEach(name => {
            stateObject.fighters[name] = { banned: name.startsWith('Mii'), selected: false };
        });
        this.state = stateObject;
        this.randomize = this.randomize.bind(this);
    }

    toggleBan(e, character) {
        e.preventDefault();
        let obj = this.state.fighters;
        obj[character] = { banned: !this.state.fighters[character].banned };
        this.setState({ fighters: obj });
    }

    randomize() {
        if (this.state.randomFighters) {
            let resetObj = this.state.fighters;
            for (let i in this.state.randomFighters) {
                resetObj[this.state.randomFighters[i]] = { banned: this.state.fighters[this.state.randomFighters[i]].banned, selected: false };
            }
            this.setState({ fighters: resetObj });
        }

        const fighters = [];
        for (let fighter in this.state.fighters) {
            if (!this.state.fighters[fighter].banned) {
                fighters.push(fighter);
            }
        }
        if (fighters.length < 5) {
            throw new Error('Too many banned fighters');
        }

        const randomFighters = [];
        for (let i = 0; i < 5; i++) {
            const randomNum = Math.floor(Math.random() * fighters.length);
            randomFighters.push(fighters.splice(randomNum, 1)[0]);
        }

        let obj = this.state.fighters;
        for (let i = 0; i < randomFighters.length; i++) {
            obj[randomFighters[i]] = { banned: false, selected: true };
        }
        this.setState({ fighters: obj });
        this.setState({ randomFighters });
    }

    renderGrid() {
        const characterGrid = [];
        for (let character in this.state.fighters) {
            characterGrid.push(
                <Character characterName={character}
                    banned={this.state.fighters[character].banned}
                    selected={this.state.fighters[character].selected}
                    key={character}
                    onClick={e => this.toggleBan(e, character)} />
            );
        }

        return characterGrid;
    }

    render() {
        return (
            <div>
                <div className="action-bar">
                    <button className="generate" type="button" onClick={this.randomize}>Randomize</button>
                </div>
                <div className="main-screen">
                    {this.state.randomFighters && this.state.randomFighters.length > 0 && <Sidebar characters={this.state.randomFighters} />}
                    <div className="character-grid">
                        {this.renderGrid()}
                    </div>
                    {this.state.randomFighters && this.state.randomFighters.length > 0 && <Sidebar characters={this.state.randomFighters} />}
                </div>
            </div>
        );
    }
}